@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&family=Noto+Serif:wght@400;700&display=swap);
.logo {
  display: block;
  max-width: 90px;
  height: auto;
  margin: 20px auto 40px auto;
}

.logo:hover {
  cursor: pointer;
}

.logoTitle {
  display: none;
}

.logoTitle {
  display: block;
  font-size: 18px;
  text-decoration: none;
  color: var(--seconadry);
  margin: -30px auto 20px auto;
  width: 150px;
}

a {
  text-decoration: none;
  color: var(--seconadry);
}

/* progress bar styles */
.progress-bar {
  height: 5px;
  background: var(--primary);
  margin-top: 10px;
  margin-bottom: 10px;
}

.loginBtn {
  display: block;
  margin: 50px auto 0;
  padding: 10px 15px;
  background-color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 4px;
  font-size: 1.2rem;
  font-family: "Noto Serif";
  color: var(--secondary);
  cursor: pointer;
}

.loginBtn:hover {
  border: 1px solid var(--primary);
  background: none;
  color: var(--primary);
  cursor: pointer;
}

/* upload form styles */
form {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  background: white;
  justify-items: center;
  align-items: center;
  min-height: 100vh;
  padding: 40px 0;
}

input,
button,
label,
textarea {
  width: 300px;
}

label input {
  height: 0;
  width: 0;
  opacity: 0;
}

input[type="text"],
textarea {
  border: 1px solid var(--secondary);
  border-radius: 4px;
  display: inline-block;
  padding: 12px 14px;
  color: var(--secondary);
  font-size: 14px;
}

textarea {
  height: 150px;
  margin-top: 30px;
}

input[type="file"] {
  display: none;
}

.custom-file-upload {
  border: 1px solid var(--primary);
  border-radius: 4px;
  display: inline-block;
  padding: 10px 14px;
  cursor: pointer;
  color: var(--primary);
  text-align: center;
  margin-top: 10px;
}

.custom-file-upload:hover {
  background-color: var(--primary);
  color: var(--secondary);
}

.output {
  height: 60px;
  font-size: 0.8rem;
}

.error {
  color: var(--error);
}

.addNewBtn {
  display: block;
  margin: 0 auto 0;
  padding: 10px 15px;
  background-color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 4px;
  font-size: 1.2rem;
  font-family: "Noto Serif";
  color: var(--secondary);
}

.addNewBtn:hover {
  border: 1px solid var(--primary);
  background: none;
  color: var(--primary);
  cursor: pointer;
}

.closeForm {
  display: block;
  margin: 50px auto 50px auto;
  padding: 10px 15px;
  background-color: var(--secondary);
  border: 1px solid var(--secondary);
  border-radius: 4px;
  font-size: 1.2rem;
  font-family: "Noto Serif";
  color: white;
  cursor: pointer;
}

.closeForm:hover {
  border: 1px solid var(--secondary);
  background: none;
  color: var(--secondary);
  cursor: pointer;
}

.submitForm {
  display: none;
  margin-top: 20px;
}

.thumbnail img {
  max-height: 100px;
  width: auto;
  border: none;
  margin: 20px auto;
  border: none;
  box-shadow: none;
}

@media only screen and (min-width: 800px) {
  .form {
    padding: 40px;
  }

  .thumbnail img {
    max-width: 400px;
    max-height: 100%;
  }

  input,
  label,
  textarea {
    width: 500px;
  }

  .addNewBtn {
    margin-top: 30px;
  }
}

/* image grid styles */
.img-grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
}

.image {
  position: relative;
}

.image::after {
  content: "";
  display: block;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.loading {
  margin: 150px auto;
  text-align: center;
  width: 100vw;
}

@media only screen and (min-width: 800px) {
  .img-grid {
    grid-template-columns: 1fr 1fr;
  }

  .image:hover {
    opacity: 0.9;
  }
}

@media only screen and (min-width: 1400px) {
  .img-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

/* modal styles */
.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: none;
}

.backdrop img {
  display: block;
  max-width: 95%;
  max-height: auto;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
}

@media only screen and (min-width: 800px) {
  .backdrop img {
    display: block;
    max-width: 60%;
    max-height: 80%;
  }
}

/* STAR RATING STYLES */
.stars {
  border: 0;
  margin: 20px 0 0;
  padding: 0;
  font-size: 100%;
}

.stars li {
  float: left;
  list-style: none;
  width: 20%;
  max-width: 35px;
  line-height: 0;
  margin-bottom: 10px;
  text-align: center;
  padding: 0 5px;
  box-sizing: border-box;
}

.stars li:first-child {
  margin-left: -2px;
}

.stars li:hover {
  cursor: pointer;
}

.stars .star {
  width: auto;
  height: 100%;
  fill: #3658a25f;
}

.stars .selected .star {
  fill: #2d4f9a;
}

.rating {
  display: block;
  width: 100%;
  height: 50px;
}

.starRating {
  display: block;
  margin: 10px auto 20px auto;
  height: 50px;
  width: 355px;
}

@media only screen and (min-width: 800px) {
  .stars li {
    max-width: 40px;
  }

  .starRating {
    width: 440px;
  }
}

/***** User *****/
.userIsLoggedIn {
  position: absolute;
  top: 15px;
  right: 15px;
  overflow: hidden;
}

.userIsLoggedIn p {
  display: none;
}

.userContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  margin-top: 50px;
}

.userContent p {
  display: block;
  font-size: 1.2rem;
  padding: 0 15px;
}

@media only screen and (min-width: 800px) {
  .userContent {
    margin-top: 0;
  }
}

.userContent img {
  border-radius: 50%;
  max-width: 70px;
  width: 100%;
  height: auto;
  display: block;
}

.userPage {
  position: fixed;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
  height: 100%;
  width: 290px;
  padding: 15px;
  margin-right: -350px;
  background: white;
  top: 0;
  right: 0;
  z-index: 999;
  transition-duration: 1s;
  overflow: hidden;
}

.show {
  margin-right: 0 !important;
  transition-duration: 1s;
  display: flex;
  flex-direction: column;
}

/**** HAMBURGER ***/
nav {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 55px;
}

nav img {
  border-radius: 50%;
  width: 55px;
}

.closeNav {
  align-self: flex-end;
  padding: 10px 15px;
  background-color: transparent;
  border: 1px solid var(--primary);
  border-radius: 50%;
  font-size: 1.2rem;
  font-family: "Verdana";
  color: var(--primary);
  width: 50px;
}

.closeNav:hover {
  align-self: flex-end;
  padding: 10px 15px;
  background-color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 50%;
  font-size: 1.2rem;
  font-family: "Verdana";
  color: white;
  width: 50px;
}

.logOutBtn {
  display: block;
  margin: 25px 0;
  padding: 10px 15px;
  background-color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 4px;
  font-size: 1.2rem;
  font-family: "Noto Serif";
  color: var(--secondary);
}

.logOutBtn:hover {
  border: 1px solid var(--primary);
  background: none;
  color: var(--primary);
  cursor: pointer;
}

.search {
  display: flex;
}

.search input {
  margin: 40px auto 0 auto;
}

.map-container {
  position: absolute;
  width: 100%;
  height: 750px;
  left: 0;
  margin-top: 50px;
}

.mapboxgl-ctrl-bottom-left,
.mapboxgl-ctrl-attrib-button,
.mapboxgl-ctrl {
  display: none;
}

@media only screen and (min-width: 1200px) {
  .map-container {
    position: block;
    width: 500px;
    height: 500px;
    left: auto;
    margin: 0;
    /* border-radius: 50%; */
    /* border: 1px solid var(--primary); */
  }
}

@media only screen and (min-width: 1600px) {
  .map-container {
    position: block;
    width: 500px;
    height: 500px;
    left: auto;
    margin: 0;
    /* border-radius: 50%; */
    /* border: 1px solid var(--primary); */
  }

  .whiskyContainer .map {
    -webkit-mask: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.2) 10%,
        white 10% 90%,
        rgba(0, 0, 0, 0.2) 100%
      ),
      linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.2) 10%,
        white 10% 90%,
        rgba(0, 0, 0, 0.2) 100%
      );
    mask: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.2) 10%,
        white 10% 90%,
        rgba(0, 0, 0, 0.2) 100%
      ),
      linear-gradient(
        rgba(0, 0, 0, 0.2) 10%,
        white 10% 90%,
        rgba(0, 0, 0, 0.2) 100%
      );
    -webkit-mask-composite: source-in; /* For Chrome */
    mask-composite: intersect; /* For Firefox */
  }
}

.whiskyDetailsContainer .whiskyItem {
  display: flex;
}

.ratingNumber {
  text-align: center !important;
  color: #2d4f9a;
  font-weight: 700 !important;
}

.whiskyContainer .whiskyItem h2 {
  text-align: center;
  font-family: "Noto Sans", Helvetica, Arial;
  font-weight: 200;
}

.whiskyContainer .whiskyItem .notes textarea {
  margin: 0;
  width: 90%;
  height: 90px;
}

.whiskyContainer .getWhiskyInfo {
  text-align: center;
  margin-bottom: 20px;
}

.whiskyContainer .map {
  display: none;
}

@media only screen and (min-width: 800px) {
  .whiskyContainer .whiskyItem {
    display: grid;
    width: 100%;
    grid-template-areas:
      "header header"
      "image details"
      "rating details"
      "notes details"
      "notes .."
      "map map";
    grid-template-rows: 40px 400px 90px 50px;
    grid-template-columns: 400px 1fr;
    grid-gap: 20px 40px;
  }

  .whiskyContainer .whiskyItem h2 {
    grid-area: header;
    margin: 0;
    text-align: left;
    font-size: 2em;
    margin-bottom: -10px;
  }

  .whiskyContainer .whiskyItem .image {
    grid-area: image;
  }

  .whiskyContainer .whiskyItem .image img {
    width: 100%;
    height: auto;
  }

  .whiskyContainer .whiskyItem .rating {
    grid-area: rating;
  }

  .whiskyContainer .whiskyItem .notes {
    grid-area: notes;
  }

  .whiskyContainer .whiskyItem .notes textarea {
    width: 370px;
    height: 150px;
  }

  .whiskyContainer .whiskyItem .whiskyDetailsContainer {
    grid-area: details;
  }

  .whiskyContainer .getWhiskyInfo {
    margin-top: 100px;
    text-align: left;
    margin-bottom: 0;
  }

  .whiskyContainer .getWhiskyInfo .searchForm input {
    width: 40%;
  }

  .whiskyContainer .getWhiskyInfo .search {
    width: 25%;
  }

  .whiskyContainer .whiskyItem .noWhiskyData,
  .whiskyContainer .whiskyItem .loadingData {
    grid-area: details;
    margin-top: 60px;
    font-weight: 100;
    text-align: center;
  }
}

@media only screen and (min-width: 1200px) {
  .whiskyContainer .getWhiskyInfo .searchForm input {
    width: 400px;
  }

  .whiskyContainer .getWhiskyInfo .search {
    width: 300px;
  }
}

@media only screen and (min-width: 1600px) {
  .whiskyContainer .whiskyItem {
    display: grid;
    width: 100%;
    grid-template-areas:
      "header header header"
      "image notes map"
      "image rating map"
      "details details map";
    grid-template-rows: auto auto 1fr;
    grid-template-columns: 400px 1fr auto;
    grid-gap: 20px;
  }

  .whiskyContainer .map {
    grid-area: map;
    display: flex;
    width: 500px;
    height: 500px;
    overflow: none;
  }

  .whiskyContainer .whiskyItem h2 {
    font-size: 3em;
  }

  .ratingNumber {
    text-align: right !important;
  }

  .rating {
    margin-top: -10px;
  }
}

.whiskyDetailsContainer li {
  list-style-type: none;
  margin-left: 0;
  margin-bottom: 20px;
}

.whiskyDetailsContainer ul {
  padding-left: 0;
}

.whiskyDetailsContainer strong {
  display: block;
  width: 100%;
}

.whiskyDetailsContainer h3 {
  margin-top: 0;
  border-bottom: 1px solid var(--primary);
  width: 100%;
}

.whiskyDetails {
  padding: 70px 0 30px 0;
}

.whiskyDescription {
  margin-bottom: 50px;
}

@media only screen and (min-width: 800px) {
  .whiskyDetailsContainer {
    display: grid;
    width: 100%;
    grid-template-areas:
      "header header"
      "about about"
      "description description"
      "search search";
    grid-template-rows: 330px auto;
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }

  .whiskyDetailsContainer h3 {
    width: 105px;
  }

  .whiskyDetails h3 {
    grid-area: header;
    width: 120px;
  }

  .whiskyDescription {
    grid-area: description;
  }

  .whiskyDetails ul {
    display: inline-block;
    margin-right: 100px;
    grid-area: about;
  }

  .getWhiskyInfo {
    grid-area: search;
    margin-bottom: 50px !important;
  }

  .whiskyDetails {
    padding: 0;
  }

  .whiskyDescription {
    margin-bottom: 0;
  }
}

@media only screen and (min-width: 1600px) {
  .whiskyDetailsContainer {
    display: grid;
    width: 100%;
    grid-template-rows: auto;
    grid-template-columns: 400px 1fr;
    grid-gap: 20px;
    grid-template-areas:
      "header header"
      "about description"
      "about search";
  }

  .whiskyContainer .whiskyItem .notes textarea {
    width: 95%;
    height: 250px;
  }

  .whiskyDetails {
    grid-area: about;
  }

  .whiskyDetails ul {
    margin: 0 50px 0 0;
  }
}

.searchForm {
  display: flex;
  flex-direction: column;
}
.searchForm input {
  margin: 0 auto;
}
.searchForm .search {
  display: block;
  margin: 20px auto;
}

.searchResults {
  display: none;
}

.displayData ul {
  padding: 0;
}

.displayData li {
  display: inline-block;
  margin: 5px;
  padding: 15px 10px;
  border: 2px solid var(--primary);
  width: 90%;
}

@media only screen and (min-width: 800px) {
  .displayData li {
    display: inline-block;
    margin: 5px;
    padding: 5px 10px;
    border: 2px solid var(--primary);
    width: auto;
  }

  .displayData li:hover {
    background: var(--primary);
    color: white;
    cursor: pointer;
  }

  .searchForm {
    flex-direction: row;
  }

  .searchForm input {
    margin: 0;
  }

  .getWhiskyInfo {
    min-width: 750px;
    display: flex;
    flex-direction: column;
  }

  .searchForm .search {
    margin: 0 0 0 20px;
  }
}

:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
  --medium: 800px;
}

/* base styles & title */
body {
  font-family: "Noto Serif";
  color: #4e4e4e;
  color: var(--secondary);
  overflow-x: hidden;
}
.App {
  width: 100%;
}

@media only screen and (min-width: 800px) {
  .App {
    margin: 0 20px;
  }
}

@media only screen and (min-width: 1400px) {
  .App {
    max-width: 1400px;
    margin: 0 auto;
  }
}

@media only screen and (min-width: 1600px) {
  .App {
    max-width: 1600px;
    margin: 0 auto;
  }
}

.title h1 {
  margin-top: 10px;
  font-size: 2.6rem;
}

.title h1,
.title p,
.logoTitle {
  text-align: center;
  font-family: "Noto Serif", Helvetica, Arial;
  font-weight: 700;
}

h1,
h2,
h3 {
  font-family: "Noto Sans", Helvetica, Arial;
  font-weight: 400;
}

.notLoggedIn {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-image: url(/whisky/static/media/login.66da4d6a.png);
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
}

.notLoggedIn .container {
  position: absolute;
  bottom: 5%;
  left: 0;
  right: 0;
}

.notLoggedIn .container h1 {
  font-size: 3em;
  width: 100%;
  color: ghostwhite;
  text-transform: uppercase;
  font-family: "Noto Sans", Helvetica, Arial, sans-serif;
  margin-bottom: -30px;
}

/* Whisky page */

